/* src/components/GameCard/GameCard.css */

.game-card {
  background-color: #2c2c2c;
  color: #fff;
  border-radius: 10px;
  padding: 1rem;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.4);
  transition: transform 0.2s, box-shadow 0.2s;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.game-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.1);
}

.game-card a {
  color: #fff;
  text-decoration: none;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.game-image {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 0.5rem;
}

.game-title {
  font-size: 1.1rem;
  font-weight: bold;
  margin-bottom: 0.3rem;
  color: #f0f0f0;
}

.description {
  font-size: 0.85rem;
  color: #bbb;
  margin-bottom: 0.6rem;
}

.meta-score,
.user-score {
  font-size: 0.9rem;
  font-weight: 600;
  margin: 0.15rem 0;
  display: inline-block;
  padding: 4px 8px;
  border-radius: 4px;
  width: fit-content;
}

/* 메타 점수 색상 분기 */
.meta-score[data-score="high"] {
  background-color: #66bb6a; /* green */
}
.meta-score[data-score="mid"] {
  background-color: #fdd835; /* yellow */
}
.meta-score[data-score="low"] {
  background-color: #ef5350; /* red */
}
